body {
  background-color: whitesmoke;
  font: 1.8rem 'Roboto', sans-serif;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.welcome-heading {
  font: 3.7rem 'Monoton', cursive;
  word-spacing: 1rem;
}

.button {
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  font-size: 20px;
  letter-spacing: 4px;
  position: relative;
  background-color: #16a085;
  border: none;
  color: #fff;
  padding: 20px;
  width: 200px;
  text-align: center;
  transition-duration: 0.4s;
  overflow: hidden;
  box-shadow: 0 5px 15px #193047;
  border-radius: 4px;
}

.button:hover {
  background: #fff;
  box-shadow: 0px 2px 10px 5px #1abc9c;
  color: #000;
}

.button:after {
  content: "";
  background: #1abc9c;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px !important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s
}

.button:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s
}

.button:focus { outline:0; }

.note {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.note-information {
  width: 80%;
  padding: 10px;
  background-color: tan;
  color: #333;
  position: relative;
  animation-name: fadeUp;
  animation-duration: 3s;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 40%);
  border-radius: 8px 0 8px 8px;
}

.note-information > .close {
  width: 2rem;
  height: 2rem;
  background-color: red;
  color: white;
  font-weight: bold;
  position: absolute;
  top: -40px;
  right: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  cursor: pointer;
  transition: .4s;
}

.note-information > .close:hover {
  transform: rotate(180deg);
  background-color: rgb(184, 36, 36);
  color: whitesmoke;
}

.note-information > img {
  max-width: 25%;
  filter: drop-shadow(1px 1px 5px #333);
}

@keyframes fadeUp {
  from {
    transform: translateY(100%);
  } to {
    transform: translateY(0);
  }
}

.main-app {
  width: 90%;
  margin: auto;
}

.main-app > .app-photo {
  max-width: 40%;
  border-radius: 8px;
  border: 1px solid #333;
  padding: 2px;
}

.main-app > form {
  width: 80%;
  padding: 15px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 18px;
  font-size: 1.3rem;
}

.main-app > form > label > input[type="radio"] {
  accent-color: #1abc9c;
}

.main-app > form > .question,
.main-app > form > input[type="submit"] {
  grid-column: 1 / -1;
}

.main-app > form > input[type="submit"] {
  width: 60px;
  padding: 8px 28px;
  justify-self: end;
  cursor: pointer;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: none;
  border: 1px solid #1abc9c;
}

.main-app > form > input[type="submit"]:hover {
  border: 2px solid #1abc9c !important;
}

.main-app > .yourscore {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: #1abc9c;
  color: whitesmoke;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .main-app {
    width: 85%;
    font-size: 1.2rem;
  }

  .main-app > .app-photo {
    max-width: 75%;
  }

  .main-app > form {
    width: 90%;
    grid-template-columns: 1fr;
  }
}

@media only screen 
  and (max-width: 330px) 
  and (max-height: 568px) {

  .welcome-heading {
    font-size: 2.8rem;
  }
  .note-information {
    font-size: 1.3rem;
  }

  .main-app > .app-photo {
    width: 50%;
  }

  .main-app > form {
    font-size: 1rem;
    gap: 10px;
  }
}